import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link, useParams } from 'react-router-dom';
import { GET_ADD_FEED } from '../../../../../actions/moderationActions';
import { SNAPUP_DETAILS_ACTION } from '../../../../../actions/addFeedActions';
import { GET_NETWORK_ICON, GET_NETWORK_NAME, generateRandomArray } from '../../../../../utils';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { Image, Badge, Container, Card, Button } from 'react-bootstrap';
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC';

const AddFeed = () => {
    const { wallId } = useParams();
    const dispatch = useDispatch()
    const addFeedData = useSelector(state => state.addFeedReducer)
    const manageFeed = useSelector(state => state.manageFeeds)

    useEffect(() => {
        dispatch(GET_ADD_FEED())
        // if (!addFeedData?.details?.wall) dispatch(SNAPUP_DETAILS_ACTION())
    }, [])

    const getLinkProps = (networkId) => {
        if (networkId === 33) return { to: "/creators" };
        if (networkId === 0) return { to: "/manual-upload" };
        return { to: `/content/addfeed/${wallId}/${networkId}` };
    };

    const renderNetworkBlocks = (network) => {
        return <div key={network.id} className="px-2 mb-4">
            <Link className='btn btn-secondary position-relative' {...getLinkProps(network.id)}>
                {network.id == 29 ? 
                <FontAwesomeIcon type={'solid'} icon={'star'} iconClass={'text-warning fs-6'}/>
                :
                <Image height={20} width={20} className='me-2' src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network.id)}.svg`)} alt={GET_NETWORK_NAME(network.id)} fluid />
                }
                {network.name}
                {network.id === 33 && <Badge bg="" className='position-absolute badge-primary py-1' style={{ top: -16, right: 10 }}>New</Badge>}
            </Link>
        </div >
    }

    return (<div className='d-flex flex-column flex-column-fluid full-content'>
        <div className="subheader">
            <Container fluid>
                <div className='f-between'>
                    <h1 className="me-2 h4 my-3">Feeds</h1>
                </div>
            </Container>
        </div>
        <Container fluid>
            <Card>
                <Card.Header className='f-between d-flex'>
                    <h2 className="me-2 h4 my-3">Add Feeds</h2>
                    <div className="f-center">
                    {manageFeed.feeds?.length > 0 ? <Link to={`/content/managefeed/${localStorage.getItem('wallId')}`} className="btn btn-primary btn-sm text-nowrap btn-min-w">
                        Manage Feeds
                    </Link> : null}
                    </div>
                </Card.Header>
                <Card.Body>
                    <div className='f-center add_feed_ m-auto'>
                        <div className='f-center flex-column mb-10'>
                            <div className="text-center mb-5">
                                <h3>Choose Source Network</h3>
                                <p>Select the social platform from which you want to add the feeds</p>
                            </div>
                            <div className="f-center flex-wrap mb-10">
                                {addFeedData?.addFeedNetworks?.networks?.length > 0 ?
                                    addFeedData.addFeedNetworks.networks.map(renderNetworkBlocks)
                                    : 
                                    generateRandomArray(12).map((_, index) => <div key={index} className="px-2 mb-4"><div className="sk_rect h-40px me-2 btn-min-w" style={{ width: 120 }}></div></div>)}

                                {addFeedData?.addFeedNetworks?.networks?.length > 0 ? <div className='px-2 mb-4'>
                                    <RequestButtonHOC>
                                        <Button variant='secondary'><FontAwesomeIcon icon={'plus'} />Request New Network</Button>
                                    </RequestButtonHOC>
                                </div>:null}
                            </div>
                        </div>
                    </div>
                </Card.Body>
            </Card>
        </Container>
    </div>
    )
}

export default AddFeed